import React, { useState, useEffect } from "react";
import ComPagination from "@/components/comPagination/ComPagination.jsx";
import {
  Input,
  Table,
  Modal,
  Form,
  Button,
  Col,
  Row,
  Checkbox,
  Alert,
  Switch,
} from "antd";
import { DeleteOutlined } from "@ant-design/icons";
import Swal from "sweetalert2";
import moment from "moment";
import { showDialog } from "../../utils/util";
import "./Api.less";

import {
  getApi,
  createdApi,
  deleteApi,
  updateWhiteList,
} from "../../api/account";

export default function Api() {
  const { TextArea } = Input;
  // 分页相关
  const [total, setTotal] = useState(200);
  const [params, setParams] = useState({
    keywords: "",
    page: 1,
    limit: 20,
    orderby: "id",
    sort: "desc",
  });

  useEffect(() => {
    getApiList();
  }, [params]);

  // 表格相关
  const [isTableLoading, setIsTableLoading] = useState(false);
  const [datasource, setDatasource] = useState([]);
  const [activeId, setActiveId] = useState(0);

  const columns = [
    {
      title: "名称",
      width: 300,
      align: "left",
      dataIndex: "name",
      className: "column-name",
    },
    {
      title: "ID",
      width: 150,
      align: "left",
      dataIndex: "id",
    },
    {
      title: "创建时间",
      dataIndex: "create_time",
      key: "create_time",
      width: 300,
      render: (create_time) => (
        <>{moment(create_time * 1000).format("YYYY-MM-DD HH:mm")}</>
      ),
    },
    {
      title: "IP白名单",
      dataIndex: "status",
      key: "status",
      width: 150,
      render: (_, record) => (
        <>
          {record.status === 1 ? (
            // 已开启
            <span className="ip-status">
              <span className="status-text-active">已开启</span>
              <a className="status-operation" onClick={() => openEdit(record)}>
                设置
              </a>
            </span>
          ) : (
            // 未开启
            <span className="ip-status">
              <span className="status-text-unactive">未开启</span>
              <a className="status-operation" onClick={() => openEdit(record)}>
                设置
              </a>
            </span>
          )}
        </>
      ),
    },
    {
      title: "操作",
      align: "left",
      width: 80,
      className: "cloumn-operate",
      fixed: "right",
      render: (_, record) => {
        return (
          <>
            {record.id === activeId ? (
              <span className="more-operation">
                <DeleteOutlined
                  style={{ color: "#5C5EE4", fontSize: "0.2rem" }}
                  onClick={() => SshDelete(record)}
                />
              </span>
            ) : (
              <span
                className="more"
                onClick={() => {
                  setActiveId(record.id);
                }}
              >
                ...
              </span>
            )}
          </>
        );
      },
    },
  ];

  // modal
  const [createSshForm] = Form.useForm();
  const [editSshForm] = Form.useForm();
  const [editApiForm] = Form.useForm();
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [isSureModalVisible, setIsSureModalVisible] = useState(false);
  const [details, setDetails] = useState({});
  const [unOpenModalVisible, setUnOpenModalVisible] = useState(false);
  const [openModalVisible, setOpenModalVisible] = useState(false);
  const [isOpen, setIsOpen] = useState(false);
  const handleCancel = () => {
    setIsModalVisible(false);
  };

  // 删除api
  const SshDelete = (record) => {
    Swal.fire({
      text: `您将删除 ${record.name},一旦删除不可找回`,
      icon: "info",
      showCancelButton: true,
      confirmButtonColor: "#5C5EE4",
      confirmButtonText: "确认",
      cancelButtonText: "取消",
      customClass: {
        icon: "ssh-icon",
        cancelButton: "ssh-cancel-button",
      },
    }).then((result) => {
      if (result.value) {
        // 调用接口执行删除
        deleteApi(record.id).then((res) => {
          if (res.data.status === 200) {
            if (res.data.msg === "删除成功") {
              Swal.fire({
                text: `成功删除 ${record.name}`,
                icon: "success",
                confirmButtonColor: "#5C5EE4",
                confirmButtonText: "确认",
              });
              setIsModalVisible(false);
              getApiList();
            } else {
              showDialog(res.data.msg, "error");
            }
          }
        });
      }
    });

    //showDialog(`您将删除${record.name},一旦删除不可找回`, "warning",alert(record.id));
  };

  // 获取Api列表
  const getApiList = () => {
    setIsTableLoading(true);
    getApi({ ...params }).then((res) => {
      console.log(res);
      if (res.data.status === 200) {
        let count = res.data.data.count;
        setTotal(() => {
          return count;
        });
        let list = res.data.data.list;
        setDatasource(list);
      }
      setIsTableLoading(false);
    });
  };

  // 换页
  const changePage = (page, limit) => {
    setParams((params) => {
      return { ...params, page, limit };
    });
  };

  // 创建Api表单提交
  const createSsh = () => {
    const formData = createSshForm.getFieldsValue();
    // 调用创建api的接口
    createdApi(formData).then((res) => {
      if (res.data.status === 200) {
        //console.log(res.data.data);
        setDetails(res.data.data);
        getApiList();
        // 初始化保存界面的勾选情况
        createSshForm.setFieldsValue({
          isSure: false
        })
        // 显示创建成功保存信息提示界面
        setIsSureModalVisible(true);
      }
      // 关闭创建表单弹窗
      setIsModalVisible(false);
    });
  };

  // 编辑ssh表单提交
  const editSsh = () => {
    // 调用 ssh编辑 接口
    const formData = editSshForm.getFieldsValue();
    console.log(formData);
  };

  // // 显示 modal
  // const showModal = () => {
  //   setIsModalVisible(true)
  // }
  const onChange = (checked) => {
    //console.log(`switch to ${checked}`)
    setIsOpen(checked);
  };

  const editApi = () => {
    const formData = editApiForm.getFieldValue();
    isOpen ? (formData.status = 1) : (formData.status = 0);
    updateWhiteList(formData).then((res) => {
      console.log(res);
      if (res.data.status === 200) {
        getApiList();
        setUnOpenModalVisible(false);
        showDialog(res.data.msg, "success");
      }
    });
  };

  const openEdit = (record) => {
    const isOpen = record.status === 1;
    setIsOpen(isOpen);
    editApiForm.setFieldsValue({
      id: record.id,
      status: record.status,
      ip: record.ip,
    });
    setUnOpenModalVisible(true);
  };

  return (
    <div className="com-card api">
      <div className="card-top">
        <div className="title">
          <p className="main-title">API</p>
          <p className="lat-title"></p>
        </div>
        <Button className="create-btn" onClick={() => {
          setIsModalVisible(true)
          createSshForm.setFieldsValue({
            name: ""
          })
        }}>
          <img
            className="create-icon"
            src={require("../../assets/images/account/yao.png")}
            alt=""
          />
          创建秘钥
        </Button>
      </div>
      <div className="card-main">
        <div className="main_table">
          <Table
            columns={columns}
            dataSource={datasource}
            pagination={false}
            loading={isTableLoading}
            rowKey={(record) => record.id}
            scroll={datasource.length !== 0 ? {
              x: "100%",
              scrollToFirstRowOnChange: true,
            } : null}
          />
        </div>
      </div>
      {/* 分页模块 */}
      {/* <ComPagination
        total={total}
        page={params}
        changePage={changePage}
      ></ComPagination> */}

      {/* 创建Api */}
      <Modal
        visible={isModalVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        width={"6.4rem"}
        maskClosable={true}
        onCancel={() => setIsModalVisible(false)}
        keyboard={true}
        getContainer={false}
      >
        <div className="modal-create-api">
          <div className="modal-title">创建秘钥</div>
          <div className="modal-form">
            <Form
              colon={false}
              size="large"
              form={createSshForm}
              onFinish={createSsh}
            >
              <Form.Item
                label="名称"
                name="name"
                rules={[
                  {
                    required: true,
                    message: "请输入名称",
                  },
                ]}
              >
                <Input />
              </Form.Item>
              <Form.Item noStyle>
                <div className="modal-form-footer flex">
                  <Button className="btn-ok" size="middle" htmlType="submit">
                    确定
                  </Button>
                  <Button
                    className="btn-no"
                    size="middle"
                    onClick={() => setIsModalVisible(false)}
                  >
                    取消
                  </Button>
                </div>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>

      {/* 创建后确认信息 modal */}
      <Modal
        visible={isSureModalVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        width={"6.4rem"}
        maskClosable={true}
        keyboard={true}
        getContainer={false}
      >
        <div className="modal-sure-api">
          <div className="modal-title">创建密钥</div>
          <div className="modal-msg">
            <div className="modal-msg-row">
              <Row>
                <Col span={4}>
                  <span className="row-name">名称</span>
                </Col>
                <Col span={20}>
                  <span className="row-msg">{details.name}</span>
                </Col>
              </Row>
            </div>
            <div className="modal-msg-row">
              <Row>
                <Col span={4}>
                  <span className="row-name">ID</span>
                </Col>
                <Col span={20}>
                  <span className="row-msg">{details.id}</span>
                </Col>
              </Row>
            </div>
            <div className="modal-msg-row">
              <Row>
                <Col span={4}>
                  <span className="row-name">Token</span>
                </Col>
                <Col span={20}>
                  <span className="row-msg">
                    <span className="row-msg-l">{details.token}</span>
                    <a
                      className="row-msg-r"
                      onClick={() =>
                        navigator.clipboard.writeText(details.token)
                      }
                    >
                      复制
                    </a>
                  </span>
                </Col>
              </Row>
            </div>
            <div className="modal-msg-row">
              <Row>
                <Col span={4}>
                  <span className="row-name">创建时间</span>
                </Col>
                <Col span={20}>
                  <span className="row-msg">
                    {moment(details.create_time * 1000).format(
                      "YYYY-MM-DD HH:mm"
                    )}
                  </span>
                </Col>
              </Row>
            </div>
          </div>
          <div className="modal-form">
            <Form
              colon={false}
              size="large"
              form={createSshForm}
              onFinish={() => setIsSureModalVisible(false)}
            >
              <Form.Item
                name="isSure"
                valuePropName="checked"
                rules={[
                  {
                    validator: (_, isSure) =>
                      isSure
                        ? Promise.resolve()
                        : Promise.reject(new Error("请保存后勾选！")),
                  },
                ]}
              >
                <div className="remind">
                  <div className="remind-label">温馨提示</div>
                  <div className="remind-main">
                    <Checkbox />
                    <span className="remind-text">
                      为了保证数据安全，系统不会存储你的原始密钥，
                      <span className="remind-i">
                        以上信息仅在创建时候显示一次
                      </span>
                      ，请务必妥善保存。
                    </span>
                  </div>
                </div>
              </Form.Item>
              <Form.Item noStyle>
                <div className="modal-form-footer flex">
                  <Button className="btn-ok" size="middle" htmlType="submit">
                    我已保存
                  </Button>
                </div>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>

      {/* ip白名单 */}
      <Modal
        visible={unOpenModalVisible}
        footer={null}
        centered={true}
        closable={false}
        style={{ borderRadius: "10px", overflow: "auto" }}
        width={"6.4rem"}
        maskClosable={true}
        onCancel={() => setUnOpenModalVisible(false)}
        keyboard={true}
        getContainer={false}
      >
        <div className="modal-unopen">
          <div className="modal-title">IP白名单设置</div>
          <div className="remind">
            <>
              <Alert
                message="IP白名单功能可以指定IP地址进行API调用，以保证秘钥安全"
                type="info"
                closable
              />
            </>
          </div>
          <div className="modal-form">
            <Form
              colon={false}
              size="large"
              form={editApiForm}
              onFinish={editApi}
            >
              <Form.Item name="id" noStyle>
                <Input style={{ display: "none" }} />
              </Form.Item>
              <Form.Item>
                <Row>
                  <Col span={4}>
                    <span className="row-name">开启状态</span>
                  </Col>
                  <Col span={20}>
                    <span className="row-msg">
                      <Switch checked={isOpen} onChange={onChange} />
                    </span>
                  </Col>
                </Row>
                <Row>
                  <Col span={4}>
                    <span className="row-name"></span>
                  </Col>
                  <Col span={20}>
                    <span className="row-msg text">
                      开启后可指定IP地址进行API调用
                    </span>
                  </Col>
                </Row>
              </Form.Item>
              {isOpen ? (
                <Form.Item
                  name="ip"
                  label="允许访问的IP"
                  rules={[
                    {
                      required: true,
                      message: "请输入ip",
                    },
                  ]}
                >
                  {isOpen ? (
                    <TextArea
                      rows={6}
                      placeholder="请输入IP地址，每行一段，如：
                            1.1.1.1
                            11.1.1-2.2.2.2"
                    />
                  ) : (
                    <></>
                  )}
                </Form.Item>
              ) : (
                <></>
              )}

              <Form.Item>
                <div className="modal-form-footer flex">
                  <Button className="btn-ok" size="middle" htmlType="submit">
                    确定
                  </Button>
                  <Button
                    className="btn-no"
                    size="middle"
                    onClick={() => setUnOpenModalVisible(false)}
                  >
                    取消
                  </Button>
                </div>
              </Form.Item>
            </Form>
          </div>
        </div>
      </Modal>
      {/* ip白名单已开启 */}
    </div>
  );
}
